export default {
	name: 'UniPopup',
	props: {
		// 开启动画
		animation: {
			type: Boolean,
			default: true
		},
		// 弹出层类型，可选值，top: 顶部弹出层；bottom：底部弹出层；center：全屏弹出层
		type: {
			type: String,
			default: 'center'
		},
		// 是否开启自定义
		custom: {
			type: Boolean,
			default: false
		},
		// maskClick
		maskClick: {
			type: Boolean,
			default: true
		}
	},
	data() {
		return {
			ani: '',
			show: false,
			tab: 0
		};
	},
	created() {},
	methods: {
		clear() {},
		open() {
			this.tab = this.tab + 1;
			console.log(this.tab);
			this.show = true;
			this.$nextTick(() => {
				setTimeout(() => {
					this.ani = 'uni-' + this.type;
				}, 30);
			});
			uni.hideTabBar({});
		},
		close(type) {
			if (!this.maskClick && type) return;
			if (type == 'o') {
				uni.showTabBar({});
				console.log(type)
			}

			this.ani = '';
			this.$nextTick(() => {
				setTimeout(() => {
					this.show = false;
				}, 300);
			});
		}
	}
};
